import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);
const homes = r => require.ensure([], () => r(require('../page/homes.vue')), 'homes');
const routers=[{
  path:'/homes',
  component:homes
}]
export default new Router({
  routers
})



